<template>
  <div style="height: 100vh" class="d-flex">
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12 below-toolbar">
          <v-card-title>
            <span class="title font-weight-light"> Login </span>
            <v-spacer />
            <!-- <v-icon>arrow_downward</v-icon> -->
          </v-card-title>
          <v-card-text>
            <v-form ref="form" lazy-validation @submit.prevent="handleSubmit">
              <v-text-field
                label="Username"
                shaped
                filled
                required
                autofocus
                v-model="name"
              />
              <v-text-field
                label="Password"
                shaped
                filled
                required
                type="password"
                v-model="password"
              />
            </v-form>
            <span v-show="!this.name || !this.password">enter your username and password</span>
            {{ msg }}
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn text @click="clear">Clear</v-btn>
            <v-btn
              color="blue darken-2"
              @click="handleSubmit"
              v-bind:disabled="!this.name || !this.password">
              Login
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "../axios";
import * as localstorageUtil from "../utils/localstorage";

export default {
  data() {
    return {
      name: "",
      password: "",
      msg: "",
    };
  },
  methods: {
    async handleSubmit() {
      //e.preventDefault()
      const data = await axios.post('auth/login', {name: this.name, password: this.password,}).then(data => {
        localstorageUtil.setItem("user", data.user);
        localstorageUtil.setItem("token", data.token);
        if (localstorageUtil.getItem("token") != null) {
          this.$emit("loggedIn");
          if (this.$route.params.nextUrl != null) {
            this.$router.push(this.$route.params.nextUrl);
          } else {
            this.$router.push({ path: 'services'});
          }
        }
        this.msg = "login succeeded";
      });

    },
    clear() {
      this.$refs.form.reset();
    },
  },
};
</script>

